import React from "react";
import { Select } from "antd"
import { AccessControlObjectType } from "types";

export default function AccessControlObjectTypeSelector(props) {
    return (
        <Select
            {...props}
            style={{ width: "100%" }}
        >
            <Select.Option value={AccessControlObjectType.Script}>Script</Select.Option>
            <Select.Option value={AccessControlObjectType.Page}>Dashboard</Select.Option>
        </Select>
    );
}
